import React, {useState, useContext} from 'react'
import { AppState } from '../App.js';
import '../index.css';

const Login = () => {
  const App = useContext(AppState);
  const {ethereum} = window;
  const [error,setError] = useState('');

  const LoginWallet =  async () => {
    try{
      await ethereum.request({method: "wallet_requestPermissions", params: [{eth_accounts: {}}]});
      const accounts = await ethereum.request({method: "eth_requestAccounts"});
      
      App.setAddress(accounts[0]);

      const chainId = await ethereum.request({method: "eth_chainId"});

      App.getBal();

      if(chainId == "0x5"){
        App.setChain("Goerli");
        App.setCurrency("GoerliEther");
        App.setSymbol("gEther");
        
        App.setPaypalContractAddress("0x49470Da9F1b7CFD0c9505F2758bdD96D8780f99f");
        App.setExplorer("https://goerli.etherscan.io/");
        App.setLogin(true);
      }else if (chainId == "0x4"){
        App.setChain("Rinkeby");
        App.setCurrency("RinkebyEther");
        App.setSymbol("rEthe");
  
        App.setPaypalContractAddress("0x49470Da9F1b7CFD0c9505F2758bdD96D8780f99f");
        App.setExplorer("https://rinkeby.etherscan.io");
        App.setLogin(true);
      } else if (chainId == "0x13881"){
        App.setChain("Polygon");
        App.setCurrency("Matic");
        App.setSymbol("Matic");
        
        App.setPaypalContractAddress("0x53572c3F29e0fB0129B3a1a8d24C0028b3911798");
        App.setExplorer("https://mumbai.polygonscan.com");
        App.setLogin(true);

      } else if(chainId == "0x61"){
        App.setChain("BSC");
        App.setCurrency("BNB");
        App.setSymbol("tBNB");

        App.setPaypalContractAddress("0x49470Da9F1b7CFD0c9505F2758bdD96D8780f99f");
        App.setExplorer("https://testnet.bscscan.com");
        App.setLogin(true);

      }else{
        setError("Can only access with Ropsten, Rinkeby, Polygon Mumbai and Binance Smart Chain (Testnet).");
        App.setLogin(false);
      }

    }catch (error){
      setError(`"${error.message}"`);
    }
  };

  return (
      <div className='min-w-full h-4/5 flex justify-center flex-col items-center'>
        <img className='h-20' src='crypto-pal.png' />

        <div className='w-1/3 h-40 mt-4 bg-black bg-opacity-90 p-2 rounded-lg shadow-lg border-opacity-40 border-4 border-black flex flex-col justify-center items-center'>
          <h1 className='text-white text-2xl font-medium text-center'>Login</h1>
          { ethereum != undefined ?
           <div onClick={LoginWallet} className='flex border-opacity-60 bg-opacity-90 text-lg font-medium border-2 border-blue-800 cursor-pointer bg-blue-800 text-white mt-4 rounded-lg justify-center items-center py-1 px-2'>
              Connect With Metamask
              <img className='h-10' src='metamask.png' />
           </div>
            :
           
            <div className='flex flex-col justify-center items-center'>
               {/* install Metamask */}
               <a target={"_blank"} href="https://chrome.google.com/webstore/detail/metamask/nkbihfbeogaeaoehlefnkodbefgpgknn">
                <div className='flex border-opacity-60 bg-opacity-90 text-lg font-medium border-2 border-blue-800 cursor-pointer bg-green-800 text-white mt-4 rounded-lg justify-center items-center py-1 px-2'>
                Install Metamask
                <img className='h-10' src='metamask.png' />
                </div>
              </a>
              <p className='text-red-600 text-lg mt-2'>Login Required Metamask Extension</p>
              
            </div>
          }
          <p className='text-red-600 text-lg mt-2'>{error}</p>
        </div>
        {/*PROJECT INFO  */}
        <div className='min-w-full h-2/5 flex justify-center flex-col items-center'>
        <div className='w-1/3 h-45 mt-4 bg-black bg-opacity-80 p-2 rounded-lg shadow-lg border-opacity-40 border-4 border-black flex flex-col justify-center items-center'>
          <p className='text-white text-2xl font-medium text-center'>
            Welcome to CryptoPal Project currently on testnet. CryptoPal was built as a web 3.0 Application to recreate the crosschain capabalities that a crypto wallet can have. With CryptoPal you can test the application as it follows: 
          </p>
          
        </div>
        </div>


        <div className='min-w-full h-1/7 flex justify-center flex-col items-center'>
        <div className='w-1/3 h-45 mt-4 bg-black bg-opacity-80 p-2 rounded-lg shadow-lg border-opacity-40 border-4 border-black flex flex-col justify-center items-center'>
          <ul className='justify-items-start'>
            <ol>
            <p className='text-white text-2xl font-medium text-center'>
            1) Send ERC20 tokens from multiple chains. 
            </p>
            </ol>
            <ol>
            <p className='text-white text-2xl font-medium text-center'>
            2) Add recipients to your CryptoPal account. 
            </p>
            </ol>

            <ol>
            <p className='text-white text-2xl font-medium text-center'>
            3) Show all the Recent Transactions. 
            </p>
            </ol>

            <ol>
            <p className='text-white text-2xl font-medium text-center'>
            4) Show Global Transactions. 
            </p>
            </ol>
          </ul>
          
          
        </div>
        </div>
        

        {/*SOCIAL MEDIA INFO */}
        <div class="wrapper">
    
            <a href="https://www.instagram.com/atlas__init__/" target="_blank" class="icon instagram">
              <div class="tooltip">Instagram</div>
              <span><i class="fab fa-instagram"></i></span>
            </a>
            <a href="https://github.com/Atlas-dev-sec" class="icon github" target="_blank">
              <div class="tooltip">Github</div>
              <span><i class="fab fa-github"></i></span>
            </a>

            <a href="https://wa.me/573126123935" target="_blank" class="icon twitter">
              <div class="tooltip">Phone</div>
              <span><i class="fa fa-phone"></i></span>
            </a>
        </div>

      </div>
  )
}

export default Login;