import {useState, createContext, useEffect} from 'react';
import Header from './components/Header';
import Main from './components/Main';
import Login from './components/Login';
import {ethers} from 'ethers';
import paypal from "./paypal/paypal.json";

const AppState = createContext();
function App() {
  const {ethereum} = window;
  const [login, setLogin] = useState(false);
  const [address, setAddress] = useState('');
  const [chain, setChain] = useState('');
  const [symbol, setSymbol] = useState('');
  const [balance, setBalance] = useState('');
  const [currency, setCurrency] = useState('');
  const [ercTokenAddress, setErcTokenAddress] = useState('');
  const [recipientAddress, setRecipientAddress] = useState('');
  const [amount, setAmount] = useState('');
  const [paypalContractAddress, setPaypalContractAddress] = useState('');
  const [error, setError] = useState('');
  const [message, setMessage] = useState('');
  const [tokenChanged, setTokenChanged] = useState(false);
  const [explorer, setExplorer] = useState('');
  const [showErc, setShowErc] = useState(false);
  const [ercLoading, setErcLoading] = useState(false);
  const [txLoading, setTxLoading] = useState(false);
  const [showRecentTx, setShowRecentTx] = useState(false);
  const [recentTx, setRecentTx] = useState({
    txhash: '',
    from: '',
    to: '',
    amount: '',
    symbol: ''
  });

  const [saveTxLoad, setSaveTxLoad] = useState(false);


  async function getBal(){
      const provider = new ethers.providers.Web3Provider(ethereum);
      const signer = provider.getSigner();
      const balance = await signer.getBalance();
      setBalance(ethers.utils.formatEther(balance));
  }
 
  const provider = new ethers.providers.Web3Provider(window.ethereum);
  const signer = provider.getSigner();

  // Accesing the data from any erc20 contract that implements this functions...
  const ERCABI = [
    "function balanceOf(address) view returns (uint)",
    "function transfer(address to, uint amount) returns (bool)",
    "function symbol() external view returns (string memory)",
    "function name() external view returns (string memory)"
  ];

  // contracts
  const ERCContract = new ethers.Contract(ercTokenAddress, ERCABI, signer);
  const paypalContract = new ethers.Contract(paypalContractAddress, paypal.output.abi, signer);

  const selectToken = async () =>{
    try {
    setErcLoading(true);
    const name = await ERCContract.name();
    const symbol = await ERCContract.symbol();
    const balance = await ERCContract.balanceOf(address);

    setBalance(ethers.utils.formatEther(balance))
    setSymbol(symbol);
    setCurrency(name);
    setTokenChanged(true);
    setErcLoading(false);
      
    } catch (error) {
      setError(error.message);
      setErcLoading(false);
    }
    
  };

  const removeToken = async () =>{
    try {
      if(chain == 'Ropsten'){
        setCurrency("RopstenEther");
        setSymbol("rEth");
      }else if (chain == 'Rinkeby'){
        setCurrency("RinkebyEther");
        setSymbol("rEth");
      }else if(chain == 'polygon'){
        setCurrency("Matic");
        setSymbol("Matic");
      }else if(chain == 'BSC'){
        setCurrency("BNB");
        setSymbol("tBNBN");
      }
      //0x4dcf5ac4509888714dd43a5ccc46d7ab389d9c23 -> human contract token address to test !!!
      setErcTokenAddress('');
      setShowErc(false);
      setTokenChanged(false);
      getBal();
      
    } catch (error) {
      setError(error.message);
    }
  };

  const transferAmount = async () => {
    setMessage('');
    setTxLoading(true);
    try{
      if(tokenChanged){
        const tx = await ERCContract.transfer(recipientAddress, ethers.utils.parseEther(amount));
        await tx.wait();
        selectToken();
        setRecentTx({
          txhash: tx.hash,
          from: address,
          to: recipientAddress,
          amount: amount,
          symbol: symbol
      });
      setShowRecentTx(true);

      }else{
        const tx = await paypalContract._transfer(recipientAddress, symbol, {value : ethers.utils.parseEther(amount)});
        await  tx.wait();
        getBal();
      }

      setMessage("Transaction succesfull");
      setAmount('');

    }catch (error){
      setError(error.message);
    }
    setTxLoading(false);

  };

  const saveTx = async () =>{
    setSaveTxLoad(true);
    try {
      const tx = await paypalContract.saveTx(recentTx.from, recentTx.to, ethers.utils.parseEther(recentTx.amount), recentTx.symbol);
      await  tx.wait();
      setMessage("Transaction saved successfully!!");

      
    } catch (error) {
      setError(error.message);
    }
    setShowRecentTx(false);
    setSaveTxLoad(false);

  };

  useEffect(() => {
    ethereum.on("chainChanged", async (chainId) => {
      if(chainId == "0x5"){
        setChain("Goerli");
        setCurrency("GoerliEther");
        setSymbol("gEther");
        setPaypalContractAddress("0x49470Da9F1b7CFD0c9505F2758bdD96D8780f99f");
        setExplorer("https://goerli.etherscan.io/");
      }else if (chainId == "0x4"){
        setChain("Rinkeby");
        setCurrency("RinkebyEther");
        setSymbol("rEthe");
        setPaypalContractAddress("0x49470Da9F1b7CFD0c9505F2758bdD96D8780f99f");
        setExplorer("https://rinkeby.etherscan.io");
      } else if (chainId == "0x13881"){
        setChain("Polygon");
        setCurrency("Matic");
        setSymbol("Matic");
        setPaypalContractAddress("0x53572c3F29e0fB0129B3a1a8d24C0028b3911798");
        setExplorer("https://mumbai.polygonscan.com");
      } else if (chainId == "0x61"){
        setChain("BSC");
        setCurrency("BNB");
        setSymbol("tBNB");
        setPaypalContractAddress("0x49470Da9F1b7CFD0c9505F2758bdD96D8780f99f");
        setExplorer("https://testnet.bscscan.com");
      }else{
        setLogin(false);
      }

      getBal();
    })

    ethereum.on("accountsChanged", async (accounts) => {
      setAddress(accounts[0]);
      
    });

  }, []);


  useEffect(() =>{
    if(tokenChanged){
      selectToken();
    }else{
      getBal();
    }
  }, [address]);

  useEffect(() =>{
    removeToken();
  }, [chain]);

  useEffect(() => {
    document.title = "CryptoPal"
  }, []);


  return (
    <AppState.Provider value={{login, setLogin, address, setAddress, chain, setChain, symbol, setSymbol, currency, setCurrency, balance, setBalance,
     getBal, ercTokenAddress, setErcTokenAddress, recipientAddress, setRecipientAddress,amount, setAmount, paypalContractAddress, setPaypalContractAddress,
     explorer, setExplorer,error, setError, message, setMessage, tokenChanged, setTokenChanged, setTokenChanged, removeToken, selectToken, transferAmount,
     showErc, setShowErc, ercLoading, setErcLoading, txLoading, setTxLoading, showRecentTx, setShowRecentTx, recentTx, setRecentTx, saveTxLoad, setSaveTxLoad, saveTx, paypalContract}}>
    <div className="min-w-full h-screen">
      { login ? 
      <div className="min-w-full min-h-full">
        {/*Main APP */}
        <Header />
        <Main />
      </div>
        :
        <Login />
      }
    </div>
    </AppState.Provider>
  );
}

export default App;
export {AppState};